<template>
  <div class="home">
    <div class="content" v-if="show">
      <h2>新建表决</h2>
      <!-- 表决标题 -->
      <el-form size="small" :model="vote" ref="voteForm">
        <el-form-item prop="title" :rules="[{ required: true, message: '请输入标题', trigger: 'blur' }]">
          <div slot="label" class="label">标题：</div>
          <el-input v-model="vote.title"></el-input>
        </el-form-item>

        <div v-for="(dis,did) in vote.discussionList" :key="did">
          <el-form-item
            :prop="'discussionList.'+did+'.content'"
            :rules="[{ required: true, message: '请输入内容', trigger: 'blur' }]"
          >
            <div slot="label" class="label">内容：</div>
            <el-input type="textarea" :rows="2" placeholder="请输入内容" v-model="dis.content"></el-input>
          </el-form-item>

          <el-row type="flex" v-for="(opt,oid) in dis.options" :key="oid">
            <el-col :span="16">
              <el-form-item
                :prop="'discussionList.'+did+'.options.'+oid+'.text'"
                :rules="[{ required: true, message: '请输入选项内容', trigger: 'blur' }]"
              >
                <div slot="label" class="label">选项{{oid+1}}：</div>
                <el-input v-model="opt.text" placeholder="输入选项内容"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-button type="primary" class="remOpt" @click="remOpt(did,oid)">删除选项</el-button>
            </el-col>
          </el-row>
          <el-row type="flex">
            <el-col :span="24">
              <el-form-item>
                <div slot="label" class="label">可选选项数</div>
                <el-input-number v-model="dis.settingObj.num" :min="1" :max="dis.options.length-1"></el-input-number>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row type="flex">
            <el-col :span="12">
              <el-button type="primary" class="remOpt" v-on:click="addOpt(did)">新增选项</el-button>
            </el-col>
            <el-col :span="12">
              <el-button type="primary" class="remOpt" @click="remDis(did)">删除议题</el-button>
            </el-col>
          </el-row>
        </div>
        <el-row type="flex">
          <el-col :span="24">
            <el-button type="primary" class="remOpt" @click="addDis()">新增议题</el-button>
          </el-col>
        </el-row>

        <el-form-item>
          <div slot="label" class="label">表决设置</div>
        </el-form-item>
        <hr />
        <el-form-item>
          <div slot="label" class="label">监票人员数：</div>
          <el-input-number v-model="vote.settingObj.setForMonitor.num" :min="0"></el-input-number>
        </el-form-item>
        <hr />
        <el-row type="flex">
          <el-col :span="4">
            <el-form-item>
              <div slot="label" class="label">口径设置：</div>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">按股数统计</div>
              <el-switch
                v-model="vote.settingObj.setForVoters.kind.weight"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">按票数统计</div>
              <el-switch
                v-model="vote.settingObj.setForVoters.kind.votes"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex">
          <el-col :span="4">
            <el-form-item>
              <div slot="label" class="label">方式设置：</div>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">占已投比例</div>
              <el-switch
                v-model="vote.settingObj.setForVoters.way.already"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">占应投比例</div>
              <el-switch
                v-model="vote.settingObj.setForVoters.way.should"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex">
          <el-col :span="4">
            <el-form-item>
              <div slot="label" class="label">查看设置：</div>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">过程中查看</div>
              <el-switch
                v-model="vote.settingObj.setForVoters.view.inProcess"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">结束后查看</div>
              <el-switch
                v-model="vote.settingObj.setForVoters.view.afterProcess"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item>
          <div slot="label" class="label">表决设置（针对监票员）</div>
        </el-form-item>

        <el-row type="flex">
          <el-col :span="4">
            <el-form-item>
              <div slot="label" class="label">口径设置：</div>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">按股数统计</div>
              <el-switch
                v-model="vote.settingObj.setForMonitor.kind.weight"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">按票数统计</div>
              <el-switch
                v-model="vote.settingObj.setForMonitor.kind.votes"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex">
          <el-col :span="4">
            <el-form-item>
              <div slot="label" class="label">方式设置：</div>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">占已投比例</div>
              <el-switch
                v-model="vote.settingObj.setForMonitor.way.already"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">占应投比例</div>
              <el-switch
                v-model="vote.settingObj.setForMonitor.way.should"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex">
          <el-col :span="4">
            <el-form-item>
              <div slot="label" class="label">查看设置：</div>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">过程中查看</div>
              <el-switch
                v-model="vote.settingObj.setForMonitor.view.inProcess"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item>
              <div slot="label" class="label">结束后查看</div>
              <el-switch
                v-model="vote.settingObj.setForMonitor.view.afterProcess"
                active-color="#13ce66"
                inactive-color="#CCC"
              ></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-button type="primary" @click="chooseVoters">选择发布范围(已选参与人数{{vote.parUser.length}})</el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-button
              type="primary"
              @click="voteSubmit('voteForm')"
              :loading="submitLoading"
              v-if="!submitDisabled"
            >保存议题</el-button>
            <el-button type="primary" disabled v-else>保存议题</el-button>
          </el-col>
          <el-col :span="12">
            <el-button type="primary" v-on:click="save">暂存议题</el-button>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div v-else>
        你不是管理员，无法操作！！！
    </div>
    <el-drawer title="选择参与人员" :visible.sync="drawer" direction="ltr" size="50%">
      <div class="treeBox">
        <el-tree :load="loadNode" :props="props" lazy show-checkbox @check="handleCheck"></el-tree>
        <el-button type="primary" @click="setVoter">确定</el-button>
        <div>
          <h3>已选人员列表：</h3>
          <p>
            <span v-for="(user,userid) in vote.parUser" :key="userid">{{user.name}}</span>
          </p>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<style scoped>
.content {
  /* background: blue; */
  max-width: 750px;
  margin: auto;
}
.label {
  font-weight: bold;
  font-size: 1.2em;
}
.remOpt {
  margin-top: 25px;
}
</style>
<script>
/* eslint-disable */
import api from "../../api.js";
import qs from "qs";
import Axios from "axios";
// @ is an alias to /src
export default {
  name: "home",
  data() {
    return {
      show:true,
      drawer: false,
      submitLoading: false,
      submitDisabled: false,
      props: {
        label: "name",
        isLeaf: "leaf"
      },
      checkedUser: [],
      vote: {
        title: "",
        copies: 0,
        parUser: [],
        totalWeight: 0,
        alreadyWeight: 0,
        operPerId: "",
        operPerName: "",
        monitorList: [],
        settingObj: {
          setForVoters: {
            kind: {
              weight: true,
              votes: true
            },
            way: {
              already: true,
              should: true
            },
            view: {
              inProcess: true,
              afterProcess: true
            }
          },
          setForMonitor: {
            num: 3,
            kind: {
              weight: true,
              votes: true
            },
            way: {
              already: true,
              should: true
            },
            view: {
              inProcess: true,
              afterProcess: true
            }
          }
        },
        state: "4", //表单状态-未开始；正常；已删除
        state2: "0", //人员参与状态-已参与；未参与
        votedUserIdList: [],
        discussionList: [
          {
            type: 1,
            content: "",
            settingObj: {
              num: 1
            },
            options: [
              {
                text: "",
                votes: 0,
                userIds: []
              },
              {
                text: "",
                votes: 0,
                userIds: []
              }
            ]
          },
          {
            type: 1,
            content: "",
            settingObj: {
              num: 1
            },
            options: [
              {
                text: "",
                votes: 0,
                userIds: []
              },
              {
                text: "",
                votes: 0,
                userIds: []
              }
            ]
          }
        ]
      }
    };
  },
  created() {
    const loading = this.$loading({
      lock: true,
      text: "自动登录中...",
      spinner: "el-icon-loading",
      background: "rgba(0, 0, 0, 0.7)"
    });

    api.login().then((data) => {
      loading.close();
      if(data.status.code==api.config().status.ok){
        //登录成功
      //不是管理员则不允许打开
      if(data.data.managerflag!=1){
        //设置是否显示页面内容
       this.show=false;
        return;
      }
      
      //保存发布人员
      this.vote.operPerName=data.data.userName;
      this.vote.operPerId=data.data.userId;
      //检测是否有暂存的表决。
      let vote = JSON.parse(window.localStorage.getItem("vote"));
      if (vote) {
        this.$confirm("检测到暂存的表决，是否继续编辑?", "打开暂存表决", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "primary"
        }).then(() => {
          this.vote = vote;
        });
      }
      }else{
        //登录失败-服务器端出错
        this.$message(data.status.msg);
      }
    });

    //
  },
  methods: {
    addOpt(did) {
      // let i = e.target.dataset.i;
      let opt = {
        text: "新增选项",
        votes: 0,
        userIds: []
      };
      this.vote.discussionList[did].options.push(opt);
    },
    remOpt(did, oid) {
      this.vote.discussionList[did].options.splice(oid, 1);
    },
    addDis() {
      let dis = {
        type: 1,
        content: "新增表决议案",
        settingObj: {
          num: 1
        },
        options: [
          {
            text: "默认选项1",
            votes: 0,
            userIds: []
          },
          {
            text: "默认选项2",
            votes: 0,
            userIds: []
          }
        ]
      };
      this.vote.discussionList.push(dis);
    },
    remDis(did) {
      api
        .confirm("不可逆操作！是否删除议题？", "不可逆操作", "确认", "取消")
        .then(() => {
          this.vote.discussionList.splice(did, 1);
        });
    },
    chooseVoters() {
      this.drawer = !this.drawer;
    },
    loadNode(node, resolve) {
      let curDeptId = undefined;
      let nodeData = undefined;
      if (node.level > 0) {
        curDeptId = node.data.id;
      }
      api
        .getDept(curDeptId)
        .then(data => {
          if (data.status.code == api.config().status.ok) {
            //加载部门数据构造部门数节点对象。
            nodeData = data.data;
            //获取部门用户数据
            api
              .getDeptUser(curDeptId)
              .then(uData => {
                if (uData.status.code == api.config().status.ok) {
                  //构造人员节点
                  uData.data.forEach(user => {
                    user.leaf = true;
                    user.userId=user.userid;
                    delete user.userid;
                  });
                  nodeData = nodeData.concat(uData.data);
                  resolve(nodeData);
                } else {
                  this.$message(data.status.msg);
                }
              })
              .catch(err => {
                this.$message.error("服务器异常，请重试！");
              });
          } else {
            this.$message(data.status.msg);
          }
        })
        .catch(err => {
          this.$message.error("服务器异常，请重试！");
        });
    },
    handleCheck(node, check) {
      //处理checkedNodes
      //第一步筛选所有人员节点。
      this.checkedUser = check.checkedNodes.filter(item => {
        return item.leaf;
      });
      //第二步，去掉重复项
      let result = [];
      let obj = {};
      for (let item of this.checkedUser) {
        if (!obj[item.userid]) {
          result.push(item);
          obj[item.userid] = 1;
        }
      }
      this.checkedUser = result;
    },
    setVoter() {
      this.vote.parUser = this.checkedUser;
      this.vote.copies = this.checkedUser.length;
    },
    //暂存表决信息
    save() {
      window.localStorage.setItem("vote", JSON.stringify(this.vote));
      this.$message({
        message: "暂存成功",
        type: "success"
      });
    },
    voteSubmit(form) {
      //校验发布范围
      let votersSize = this.vote.parUser.length;
      if (!votersSize) {
        //提示未选择人员。
        this.$alert("请先选择发布范围！", "提示", {
          confirmButtonText: "确定",
          callback: action => {}
        });
      } else {
        //提交操作
        this.submitLoading = true;
        this.$refs[form].validate(valid => {
          if (valid) {
            this.submitDisabled = true;
            //发送请求保存表决
            let param = JSON.stringify({ vote: this.vote });
            Axios({
              method: "post",
              data: qs.stringify({ param: param }),
              url: api.config().domain + "/buildNewVote",
              withCredentials: true,
              headers: {
                "Content-Type":
                  "application/x-www-form-urlencoded; charset=UTF-8"
              }
            })
              .then(response => {
                this.submitLoading = false;
                let data = response.data;
                console.log(api);
                if (data.code == api.config().status.ok) {
                  this.$alert(
                    "保存成功，请在钉钉手机APP进行查看和发布！",
                    "提示",
                    {
                      confirmButtonText: "确定",
                      callback: action => {
                        window.location.reload();
                      }
                    }
                  );
                } else {
                  this.$message({
                    message: data.msg,
                    type: "error"
                  });
                }
              })
              .catch(error => {
                window.console.log(JSON.stringify(error));
              });
          } else {
            return false;
          }
        });
      }
    }
  }
};
</script>
